.App {
  text-align: center;
}
::-webkit-scrollbar {
  width: 8px;
  height: 0px;
}

::-webkit-scrollbar-track {
  border: 7px solid #838383;
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb {
  background: rgb(47, 53, 92);
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(50, 72, 90);
}
